.jumbotron {
    background-image: url("/assets/img/just-waves.png");
    text-align: center;
    height: 50rem;
    padding: 18rem;
}
nav {
    background-color: dodgerblue !important;
    border-bottom: 1px solid rgb(190, 190, 190) !important;
}

.nav-item {
    text-align: center;
    margin-top: 8%;
    height: 50px;
    font-size: 20px;
    font-family: Arial, Helvetica, sans-serif;
}
.box {
    position: relative;
    display: inline-block;
    width: 190px;
    height: 190px;
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
    margin: 25px 20px 25px 20px;
    background-size: cover;
    cursor: pointer;
}

.box::after {
    content: "";
    border-radius: 5px;
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
    opacity: 0;
    transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
}

.box:hover {
    -webkit-transform: scale(1.25, 1.25);
    transform: scale(1.25, 1.25);
}

.box:hover::after {
    opacity: 1;
}
html {
    overflow: hidden;
    height: 100%;
}

body {
    height: 100%;
    overflow: auto;
}

main {
    text-align: center;
    background-image: url("/assets/img/snorlax-bg.jpg");
    background-position: center;
    background-size: cover;
}
